.App {
  margin-top: 15px;
  text-align: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px);
  color: #282c34;
}

.clientCard{
    background: white;
    display:inline-block;
    margin:10px;
    border:1px solid #ccc;
    box-shadow:0 2px 2px #aaa;
    width:200px;
    padding:20px;
    cursor: pointer;
  }

  table{
    border: 1px solid #282c34;
    margin-top: 12px;
    width:1100px;
  }

  th{
      background: #282c34;
      color:white;
      padding-bottom: 8px;
  }

  td{
    cursor: pointer;
    padding-right: 65px;
    padding-left:65px;
    width:275px;
  }

  .rowDark{
      background: #f1f1f1;
  }